.dialogs-message-form__inner {
  font-family: 'Inter', sans-serif;
  width: 100%;
  display: flex;
  margin-left: 15px;
}

.dialogs-message-form__textarea {
  color: #1d2939;
  padding-left: 15px;
  width: 100%;
  height: 40px;
  font-size: 14px;
  border: none;
  border-radius: 50px;
  background-color: #ecf3ff;
}

.dialogs-message-form__btn {
  width: 162px;
  height: 36px;
  font-size: 14px;
  border-radius: 50px;
  margin: 0px 5px 0px 5px;
}
